.accordion {
    .accordion-button {
        font-size: inherit !important;
    }

    div {
        button[aria-expanded="true"] {
            .collapse-close {
                display: none;
            }

            .collapse-open {
                display: block;
            }
        }

        button[aria-expanded="false"] {
            .collapse-open {
                display: none;
            }

            .collapse-close {
                display: block;
            }
        }
    }
    .accordion-body {
        color: $h-color;
    }
}

// custom
.button[aria-expanded="true"] {
    .collapse-close {
        display: none;
    }

    .collapse-open {
        display: inline-block;
    }
}

.button[aria-expanded="false"] {
    .collapse-open {
        display: none;
    }

    .collapse-close {
        display: inline-block;
    }
}
